.headerContainer {
    position: relative;
    width: 100%;
    top: 0;
    height: 400px;
    background-image: url("../assets/image/1/арх 3-Рисунок 4.jpg");
    background-size: cover;
    background-position: center;
    min-width: 1650px;
}

.headerBlock {
    position: relative;
    background-color: #0d193e;
    opacity: 0.8;
    height: 100%;
    color: #ffe;
    min-width: 1450px;
}

.headerInfo {
    min-width: 1450px;
    padding: 0 100px;
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: space-between;

}

.name {
    max-width: 25%;
    font-size: 55px;
    text-align: start;
    font-weight: bold;
    color: #ffb400;
    letter-spacing: 3px;
    font-style:italic;
    margin-left: 100px;
}

.text {
    max-width: 25%;
    font-size: 35px;
    font-style:italic;
    letter-spacing: 3px;
    font-weight: 500;
}

.contact {
    max-width: 25%;
    text-align: end;
    margin-right: 100px;
    font-size: 25px;

}
